// 

// token
export function setToken(token) {
  uni.setStorageSync("token", token);
}

// token
export function getToken() {
  return uni.getStorageSync("token")
}

// token
export function clearToken() {
  uni.removeStorageSync("token")
}

// 
export function setUserInfo(userInfo) {
  uni.setStorageSync("userInfo", JSON.stringify(userInfo));
}

// 
export function getUserInfo() {
  return JSON.parse(uni.getStorageSync("userInfo"));
}


//  token
export const whiteList = [
	'/pages/login/index', // 
	'/pages/home/index/index', // 
	'/pages/home/resell/index', // 
	'/pages/home/auction/index', // 
	'/pages/home/seriesDetail/index', // 
	'/pages/home/skuDetail/index' // 
]
//
const loginPage = "/pages/login/index"
export function initPermission() {
  // 
  // "switchTab"
  const list = ["navigateTo", "redirectTo", "reLaunch"];
  //,uni.navigateTo,uni.redirectTo,uni.reLaunch 3
	list.forEach(item => {
		uni.addInterceptor(item, {
			invoke(e) { // 
				//token
        const token = getToken()
					//url"?""?"
				let url = e.url.split('?')[0];
				// whiteList
				if (whiteList.includes(url)) {
					return e
				} else {
					//
					if (!token) {
						uni.navigateTo({
							url: loginPage
						})
						return false
					} else {
						return e
					}
				}
			},
			fail(err) { //  
				console.log(err);
			}
		})
	})
}
