export const accAdd = (num1, num2) => {
  let r1
  let r2
  try {
    r1 = num1.toString().split('.')[1].length
  } catch (e) {
    r1 = 0
  }
  try {
    r2 = num2.toString().split('.')[1].length
  } catch (e) {
    r2 = 0
  }
  const m = Math.pow(10, Math.max(r1, r2))
  const n = r1 >= r2 ? r1 : r2
  return (Math.round(num1 * m + num2 * m) / m).toFixed(n)
}

// 
export const accSub = (num1, num2) => {
  let r1
  let r2
  try {
    r1 = num1.toString().split('.')[1].length
  } catch (e) {
    r1 = 0
  }
  try {
    r2 = num2.toString().split('.')[1].length
  } catch (e) {
    r2 = 0
  }
  const m = Math.pow(10, Math.max(r1, r2))
  const n = r1 >= r2 ? r1 : r2
  return (Math.round(num1 * m - num2 * m) / m).toFixed(n)
}

// 
export const accMul = (arg1, arg2) => {
  var m = 0,
    s1 = arg1.toString(),
    s2 = arg2.toString()
  try {
    m += s1.split('.')[1].length
  } catch (e) {
    // console.error('')
  }
  try {
    m += s2.split('.')[1].length
  } catch (e) {
    // console.error('')
  }
  return (
    (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
    Math.pow(10, m)
  )
}

// 
export const accDiv = (arg1, arg2) => {
  var t1 = 0,
    t2 = 0,
    r1,
    r2
  try {
    t1 = arg1.toString().split('.')[1].length
  } catch (e) {
    // console.error('')
  }
  try {
    t2 = arg2.toString().split('.')[1].length
  } catch (e) {
    // console.error('')
  }

  r1 = Number(arg1.toString().replace('.', ''))
  r2 = Number(arg2.toString().replace('.', ''))
  return (r1 / r2) * Math.pow(10, t2 - t1)
}

// n
export const toFixed = (number, n) => {
  n = +n || 0
  return Math.round(number * Math.pow(10, n)) / Math.pow(10, n)
}

// 
export const toYuan = (val) => {
  return isNaN(val) ? 0 : accDiv(val, 100);
}

// storage
export function setStorage(key, val) {
  uni.setStorageSync(key, JSON.stringify(val));
}
// storage
export function getStorage(key) {
  return JSON.parse(uni.getStorageSync(key));
}

// storage
export function removeStorage(key) {
  uni.removeStorageSync(key);
}

// 
export const getQueryParams = (params, list) => {
  return {
    ...params,
    pageNo: list.length > 0 ? params.pageNo : params.pageNo - 1,
  };
}

// 
export const getTel = (tel) => {
	var reg = /^(\d{3})\d{4}(\d{4})$/;  
	return tel.replace(reg, "$1****$2");
}