export default {
	data() {
		return {
			//
			//share
			share: {
				title: '',//
				path: `/pages/home/index/index`,  //
				imageUrl: 'https://img.u7ka.com/img/web/1/2023-05-15/1684119437256_qw_share.jpg',  //5:4
			}
		}
	},
	onShareAppMessage(res) { //
		let that = this
		// 
		let pages = getCurrentPages(); //
		let nowPage = pages[pages.length - 1]; //
		// let prevPage = pages[pages.length - 2]; //
		that.share.path = `/${nowPage.route}`
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
			success(res) {
				console.log('success(res)==', res);
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				console.log('fail(res)==', res);
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
	onShareTimeline(res) { //
		let that = this
		// 
		let pages = getCurrentPages(); //
		let nowPage = pages[pages.length - 1]; //
		// let prevPage = pages[pages.length - 2]; //
		that.share.path = `/${nowPage.route}`
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
			success(res) {
				console.log('success(res)==', res);
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				console.log('fail(res)==', res);
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
}