// vmvuex
module.exports = (vm) => {
  // 
  uni.$u.http.setConfig((config) => {
      /* config */
      config.baseURL = process.env.NODE_ENV === 'development' ? 'https://free-api-test.u7ka.com' : 'https://free-api.u7ka.com'; /*  */
      config.custom.auth = true
      return config
  })

  // 
  uni.$u.http.interceptors.request.use((config) => { // async await 
    // dataundefined{}
    config.data = config.data || {}
    // customtoken
    if(config?.custom?.auth) {
      // vmvuexvm.$store.state
      config.header.token = uni.getStorageSync("token") || ''; // vm.$store.state.userInfo.token
    }
    return config 
  }, config => { // async await 
    return Promise.reject(config)
  })

  // 
  uni.$u.http.interceptors.response.use((response) => { /*  async await */
    const data = response.data

    // 
    const custom = response.config?.custom
    if (data.code !== 0) {  // 200
      // customtoastfalsetoast
      if (custom.toast !== false) {
        if (data.message.length > 20) {
          uni.showModal({
            title: '',
            content: data.message,
            showCancel: false,
            confirmText: ''
          });
          
        } else {
          uni.$u.toast(data.message)
        }
      }
      if (data.code === 401 || data.code === 402) {
        uni.navigateTo({
          url: '/pages/login/index'
        })
      }
      // catchreject
      if (custom?.catch) {
        return Promise.reject(data)
      } else {
        // pendingpromisecatch
        return new Promise(() => { })
      }
    }

    return data.data === undefined ? {} : data.data
  }, (response) => { 
    //  statusCode !== 200
    return Promise.reject(response)
  })
}